<template>
  <div id="SFH_Device">
    <div id="search">
      <el-form :inline="true">
        <el-row>
          <el-form-item label="设备编码">
            <el-input size="small"
              placeholder="请输入内容"
              v-model="input"
              clearable>
            </el-input>
          </el-form-item>
          <el-button class="el-icon-search" size="small" type="success" round>搜索</el-button>
        </el-row>
      </el-form>
    </div>
    <div id="table_">
          <el-table
            :data="tableList"
            style="width: 100%">
            <el-table-column
              prop="device_code"
              label="设备编码"
              width="180">
            </el-table-column>
            <el-table-column
              prop="micro_stop_time"
              label="微停时长"
              width="180">
            </el-table-column>
            <el-table-column
              prop="fault_time"
              label="故障时长">
            </el-table-column>

            <el-table-column
              prop="running_time"
              label="开机时长"
              width="180">
            </el-table-column>
            <el-table-column
              prop="stop_time"
              label="停机时长"
              width="180">
            </el-table-column>
            <el-table-column
              prop="work_time"
              label="工作时长">
            </el-table-column>

            <el-table-column
              prop="lack_energy_time"
              label="缺少能源时长"
              width="180">
            </el-table-column>
            <el-table-column
              prop="lack_tool_time"
              label="缺少刀具时长"
              width="180">
            </el-table-column>
            <el-table-column
              prop="wait_quality_testing_time"
              label="等待质检时长">
            </el-table-column>

            <el-table-column
              prop="id"
              label="操作">
                <template slot-scope="scope">
                  <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
                </template>
            </el-table-column>
          </el-table>
    </div>
  </div>
</template>

<script>
  export default{
    name: 'SFHDevice',
    data(){
      return {
        tableList : [
          {
            device_code : 'HT3033',
            micro_stop_time : 0,
            fault_time : 0,
            running_time : 0,
            stop_time : 0,
            work_time : 0,
            lack_energy_time : 0,
            lack_tool_time : 0,
            wait_quality_testing_time : 0,
            id: '1'
          }
        ],
        input: ''
      }
    },
    methods:{
      handleClick : function(row){
        console.log(row)
      }
    }
  }
</script>

<style>

  #search{
    height: 80px;
    line-height: 80px;
  }
  .el-input__inner{
    width: 180px;

  }
  .el-form-item__content{
    line-height: 80px;
  }
</style>



